@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: rgb(221, 221, 221);
  font-family: 'Lato', sans-serif;
}
@font-face {
  font-family: 'poke-hollow';
  src: url(/pokedex/static/media/Pokemon-Hollow.47f77ce9.ttf);
}

@font-face {
  font-family: 'poke-solid';
  src: url(/pokedex/static/media/Pokemon-Solid.5d60be1f.ttf);
}

.header {
  display: flex;
  width: 100%;
  height: 70px;
  background-color: #F0ECE3;
}

.logo {
  margin-left: 3%;
  color: rgb(233, 233, 1);
  text-shadow: 3px 0px 0px rgb(0, 0, 211);
  letter-spacing: 10px;
  font-family: 'poke-solid';
}

.input-box {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
  margin-left: auto;
  margin-right: 20px;
}

.input-box input {
  width: 80%;
  background-color: rgb(221, 221, 221);
  border: none;
  border-radius: 20px;
  height: 50%;
  font-size: 24px;
}

.select {
  margin-left: 5px;
  text-align: center;
  height: 40px;
}

.main {
  margin-top: 5%;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 80%;
  height: 100%;
}

.main.not-grid {
  margin-top: 2%;
  margin-bottom: 2%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading {
  position: absolute;
  width: 5vw;
  height: 5vw;
  top: 45%;
  left: 45%;
  border-radius: 50%;
  border: 2vw solid transparent;
  border-top: 2vw solid rgb(104, 212, 255);
  border-left: 2vw solid rgb(104, 212, 255);
  border-right: 2vw solid rgb(104, 212, 255);
  z-index: 1;
  animation: loading 2s linear infinite;
}

.card {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 20vw;
  height: 20vw;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-right: 10px;
  cursor: pointer;
  -webkit-filter: brightness(83%);
          filter: brightness(83%);
}

.card:hover {
  box-shadow: 0px 0px 10px 5px rgb(102, 102, 102);
}

.name {
  z-index: 1;
  color: white;
}

.id {
  z-index: 1;
  position: absolute;
  right: 10%;
  top: 10%;
  color: rgb(88, 88, 88);
}

.types {
  position: absolute;
  top: 40%;
  left: 5%;
  width: 80%;
  height: 60px;
  z-index: 1;
}

.type {
  display: grid;
  place-items: center;
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px;
  background-color: rgba(255, 255, 255, 0.349);
  color: white;
  border-top-right-radius: 10px;
  border-radius: 10px;
  margin-bottom: 7%;
}

.sprite {
  width: 60%;
  height: 60%;
}

@media (max-width: 768px) {
  .main {
    grid-template-columns: 1fr 1fr;
  }

  .main.not-grid {
    width: 100%;
  }

  .card {
    width: 40vw;
    height: 40vw;
  }
}

@media (max-width: 425px) {
  .main {
    grid-template-columns: 1fr;
  }

  .card {
    width: 80vw;
    height: 80vw;
  }
}

@keyframes loading {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.panel {
  border-radius: 10px;
  margin: 0;
  width: 70%;
  background-color: rgb(129, 53, 53);
}

.container {
  display: flex;
  flex-direction: column;
}

.header-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.return {
  margin: 20px 0 0 20px;
  color: white;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  font-size: 40px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.large-name {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  color: white;
  font-size: 56px;
  margin: 0;
  margin-bottom: 10px;
  z-index: 1;
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-left: 20px;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
}

.small-box {
  display: flex;
  flex-direction: column;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
}

.box .types {
  margin-left: 8%;
  position: relative;
  display: flex;
  width: 30%;
}

.box .type {
  padding: 10px;
  border-radius: 20px;
  margin-right: 20px;
}

.header-content .id {
  position: static;
  position: initial;
  margin-left: auto;
  margin-right: 8%;
  color: white;
  font-size: 24px;
}

.large-sprite {
  position: relative;
  top: -10vh;
  align-self: center;
  width: 40%;
}

.panel-content {
  width: 100%;
  border-radius: 10px;
  border-top-right-radius: 20px;
  border-top-left-radius: 20px;
  background-color: white;
}

.labels {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 50px;
}

.about-btn, .base-btn, .moves-btn {
  font-weight: bold;
  color: black;
  background-color: transparent;
  border: none;
  height: 100%;
  padding: 0 5px;
  cursor: pointer;
}

.about-btn.active, .base-btn.active, .moves-btn.active {
  border-bottom: 2px solid blue;
}

.content {
  margin-top: 50px;
}

.content-box {
  width: 100%;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.text {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.title {
  color: rgb(77, 77, 77);
  margin: 0;
  margin-left: 10%;
}

@media (max-width: 768px) {
  .large-name {
    font-size: 36px;
  }

  .panel {
    width: 90%;
  }

  .box .type {
    height: 30px;
    padding: 5px;
  }

  .box .id {
    margin-top: 0;
  }

}

@media (max-width: 425px) {
  .panel {
    width: 100%;
  }

  .large-name {
    font-size: 26px;
  }

  .box .type {
    height: 20px;
    padding: 2px;
  }
}
